import { Text, Img } from "../../styledCpns/DataDisplay";
import { wikia } from "../../data/misc";
import characters from "../../data/characters";

export default function ConsList(props) {
  const { name, constellation } = props.char;
  return (
    <div className="full-h flex-col justify-between">
      {characters[name].constellation.map((cons, i) => (
        <div
          key={i}
          className={
            "flex align-center" + (constellation >= i + 1 ? "" : " unavailable")
          }
        >
          <Img
            className="my-2 mr-3 pointer"
            width="3.25rem"
            src={wikia + cons.image + ".png"}
            alt=""
            onClick={() => props.handleClick(i)}
          />
          <Text
            className="pointer"
            variant="h6"
            fontWeight={700}
            onClick={() => props.handleClick(i)}
          >
            {cons.name}
          </Text>
        </div>
      ))}
    </div>
  );
}
