import styled from "styled-components";
import { useContext, useEffect, useState } from "react";
import { SetDBCt } from "../../../App";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar } from "../../CommonCpns";
import MyArtCard from "../../Readers/MyArtCard";
import ArtInventory from "../../Readers/ArtInventory";
import ArtOverview from "./ArtOverview";
import { colorCodes } from "../../../data/misc";
import { deepCopy } from "../../../helpers/misc";

const HiddenSpace = styled.div`
  display: flex;
  align-items: center;
  width: 0;
  transition: width 200ms ease-in-out;
  &.show {
    width: 20.375rem;
  }
`;

export default function SectionArt(props) {
  const { owner, art, artIDs } = props;
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  const [artInvType, setArtInvType] = useState(null);
  const [shownI, setShownI] = useState(null);
  const [showing, setShowing] = useState(false);

  useEffect(() => {
    setShowing(false);
    setShownI(null);
  }, [owner]);

  return (
    <div className="ml-3 flex">
      <ArtOverview
        art={art}
        setArtInvType={setArtInvType}
        shownI={shownI}
        setShownI={setShownI}
        showing={showing}
        setShowing={setShowing}
      />
      <HiddenSpace className={"hide-sb " + (showing ? "show" : "")}>
        {shownI !== null && (
          <HiddenCard
            artP={art.pieces[shownI]}
            ownerIndex={props.ownerIndex}
            setShownI={setShownI}
            setShowing={setShowing}
            setArtInvType={setArtInvType}
          />
        )}
      </HiddenSpace>
      {Boolean(artInvType) && (
        <ArtInventory
          artType={artInvType}
          artIDs={artIDs}
          owner={owner}
          close={() => setArtInvType(null)}
          btnText="Switch"
          btnClick={(targetArt) => {
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts[
                newArts.findIndex((art) => art.ID === targetArt.ID)
              ].user = owner;
              const index = newArts.findIndex(
                (art) => art.ID === artIDs[artInvType]
              );
              if (index !== -1) newArts[index].user = targetArt.user;
              return newArts;
            });
            setMyChars((prev) => {
              const newChars = deepCopy(prev);
              newChars[props.ownerIndex].artIDs[artInvType] = targetArt.ID;
              if (targetArt.user) {
                for (let unit of newChars) {
                  if (unit.name === targetArt.user) {
                    unit.artIDs[artInvType] = artIDs[artInvType];
                    break;
                  }
                }
              }
              return newChars;
            });
          }}
        />
      )}
    </div>
  );
}

const StyledDiv = styled.div`
  border-left: 2px solid ${colorCodes.cardBg2};
  border-radius: 0 0.5rem 0.5rem 0;
  padding: 1rem 0.75rem;
  background-color: ${colorCodes.cardBg1};
`;

function HiddenCard(props) {
  const { artP, ownerIndex } = props;
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  if (!artP) return null;
  return (
    <StyledDiv>
      <Box className="hide-sb" width="18.75rem" height="24.5rem">
        <MyArtCard art={artP} mutable={true} />
      </Box>
      <ButtonBar
        className="mt-3"
        texts={["Remove", "Switch"]}
        handlers={[
          () => {
            props.setShowing(false);
            setTimeout(() => {
              props.setShownI(null);
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                newChars[ownerIndex].artIDs[artP.type] = null;
                return newChars;
              });
              setMyArts((prev) => {
                const newArts = deepCopy(prev);
                newArts[
                  newArts.findIndex((art) => art.ID === artP.ID)
                ].user = null;
                return newArts;
              });
            }, 200);
          },
          () => props.setArtInvType(artP.type)
        ]}
      />
    </StyledDiv>
  );
}
