import { useState } from "react";
import { Box, CardCarpet } from "../../../styledCpns/Layout";
import { Img } from "../../../styledCpns/DataDisplay";
import { SetBonus, ButtonBar } from "../../CommonCpns";
import ArtAllStats from "./ArtAllStats";
import artifacts from "../../../data/artifacts";
import { wikia, artifactIcons, artTypes } from "../../../data/misc";

export default function ArtOverview(props) {
  const [statsOn, setStatsOn] = useState(false);
  const { pieces, sets } = props.art;
  return (
    <CardCarpet className="relative">
      <Box className="flex-wrap justify-center " width="18.75rem">
        {[0, 2, 4, 1, 3].map((index, i) => (
          <ArtPiece
            key={i}
            artP={pieces[index]}
            index={index}
            setArtInvType={props.setArtInvType}
            shownI={props.shownI}
            setShownI={props.setShownI}
            showing={props.showing}
            setShowing={props.setShowing}
          />
        ))}
      </Box>
      <Box className="mt-3 hide-sb" width="18.75rem" height="14rem">
        <SetBonus sets={sets} />
      </Box>
      <ButtonBar
        className="mt-3"
        variants={["positive"]}
        texts={["Stats Bonus"]}
        handlers={[() => setStatsOn(true)]}
      />
      {statsOn && <ArtAllStats close={() => setStatsOn(false)} />}
    </CardCarpet>
  );
}

function ArtPiece(props) {
  const { artP, index, shownI } = props;
  let content;
  if (artP) {
    const { icon, rarity = "Gold" } = artifacts[artP.set][artP.type];
    const chosen = index === shownI;
    content = (
      <Box
        className={"p-1 pointer flex " + (chosen ? "glowing" : "cmn-shadow")}
        borderRadius="50%"
        bgImg={rarity}
        onClick={() => {
          if (chosen) {
            props.setShowing(false);
            setTimeout(() => props.setShownI(null), 200);
          } else {
            props.setShowing(true);
            props.setShownI(index);
          }
        }}
      >
        <img src={wikia + icon + ".png"} alt="" />
      </Box>
    );
  } else {
    const artT = artTypes[index];
    content = (
      <Img
        className="p-3 pointer glow-on-hover"
        width="100%"
        borderRadius="50%"
        bgColor="cardBg3"
        src={wikia + artifactIcons[artT] + ".png"}
        alt=""
        onClick={() => props.setArtInvType(artT)}
      />
    );
  }
  return (
    <Box className="px-2 flex" width="6.25rem" height="5.25rem">
      {content}
    </Box>
  );
}
