import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Constellation 1",
    desc: () => (
      <>
        The <LGreen>CRIT Rate</LGreen> of Kaeya's{" "}
        <LGreen>Normal and Charged Attacks</LGreen> against opponents affected
        by Cryo is increased by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.NA.CR += 0.15;
      bonuses.CA.CR += 0.15;
    }
  }
];
