import { useContext } from "react";
import { SetDBCt } from "../../../App";
import { Box, CardCarpet } from "../../../styledCpns/Layout";
import ConsList from "../../Readers/ConsList";
import { deepCopy } from "../../../helpers/misc";

export default function SectionCons(props) {
  const { setMyChars } = useContext(SetDBCt);
  const handleClick = (i) =>
    setMyChars((prev) => {
      const newChars = deepCopy(prev);
      const { constellation } = newChars[props.charI];
      newChars[props.charI].constellation = constellation === i + 1 ? i : i + 1;
      return newChars;
    });
  return (
    <CardCarpet className="ml-3">
      <Box className="px-1" width="18.75rem" height="28.35rem">
        <ConsList char={props.char} handleClick={handleClick} />
      </Box>
    </CardCarpet>
  );
}
