import { Collapse } from "@material-ui/core";
import { useState, useContext } from "react";
import { MyArtsCt, SetDBCt } from "../../App";
import { Select, IconBtn } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import { TypeFilter, ButtonBar } from "../CommonCpns";
import { elmtDmgBnes } from "../../data/misc";
import { deepCopy } from "../../helpers/misc";

const stats = [
  "No Priority",
  "ATK",
  "ATK%",
  "DEF",
  "DEF%",
  "HP",
  "HP%",
  "CRIT Rate",
  "CRIT DMG",
  "Elemental Mastery",
  "Energy Recharge",
  "Healing Bonus",
  "Physical DMG Bonus",
  ...elmtDmgBnes
];

export default function BtnBar(props) {
  const { setMyArts } = useContext(SetDBCt);
  const [dropped, setDropped] = useState(false);

  const typeFiller = (
    <TypeFilter forWp={false} types={props.types} setTypes={props.setTypes} />
  );

  const selectLine = (
    <SelectLine
      set={props.set}
      setSet={props.setSet}
      stat={props.stat}
      setStat={props.setStat}
    />
  );

  return (
    <div className="btn_bar full-w">
      <ButtonBar
        className="mr-3"
        distance="ml-3"
        texts={["Add", "Sort"]}
        variants={["positive", "positive"]}
        handlers={[
          props.add,
          () =>
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts.sort((a, b) => sortArtifacts(a, b));
              return newArts;
            })
        ]}
      />
      {window.innerWidth >= 550 && typeFiller}
      {window.innerWidth >= 962 ? (
        selectLine
      ) : (
        <IconBtn
          className="ml-1"
          icon="fas fa-ellipsis-h"
          bgColor={dropped ? "green" : "white"}
          onClick={() => setDropped(!dropped)}
        />
      )}
      <AbsoluteBox className="full-w" top="100%" left={0} zIndex={2}>
        <Collapse in={dropped}>
          <Box className="px-3 py-4 cmn-shadow" bgColor="cardBg2">
            {window.innerWidth < 550 && typeFiller}
            {selectLine}
          </Box>
        </Collapse>
      </AbsoluteBox>
    </div>
  );
}

function SelectLine(props) {
  const myArts = useContext(MyArtsCt);
  const cName = (window.innerWidth < 550 ? "mt-4 " : "") + "px-2 pt-2 pb-1";
  return (
    <div className="flex-wrap">
      <div className="mr-3">
        <Select
          className={cName}
          color="white"
          optColor="white"
          bgColor="cardBg3"
          value={props.set}
          onChange={(e) => props.setSet(e.target.value)}
        >
          {["All", ...getSets(myArts)].map((opt, i) => (
            <option key={i}>{opt}</option>
          ))}
        </Select>
      </div>
      <div>
        <Select
          className={cName}
          color="white"
          optColor="white"
          bgColor="cardBg3"
          value={props.stat}
          onChange={(e) => props.setStat(e.target.value)}
        >
          {stats.map((opt, i) => (
            <option key={i}>{opt}</option>
          ))}
        </Select>
      </div>
    </div>
  );
}

function sortArtifacts(a, b) {
  if (a.level !== b.level) {
    return b.level - a.level;
  }
  if (a.type !== b.type) {
    const type = {
      flower: 5,
      plume: 4,
      sands: 3,
      goblet: 2,
      circlet: 1
    };
    return type[b.type] - type[a.type];
  }
  return b.set.localeCompare(a.set);
}

function getSets(arts) {
  const result = [];
  for (let art of arts) {
    if (!result.includes(art.set)) {
      result.push(art.set);
    }
  }
  return result;
}
