import styled from "styled-components";
import { colorCodes } from "../data/misc";

export const Box = styled.div`
  width: ${(props) => props.width};
  height: ${(props) => props.height};
  border: ${(props) => props.border};
  border-radius: ${(props) => props.borderRadius};
  box-shadow: ${(props) => props.boxShadow};
  background-color: ${(props) => colorCodes[props.bgColor] || props.bgColor};
  background-image: ${(props) =>
    props.bgImg
      ? {
          Gold: "linear-gradient(to bottom, #a68746, #c5a03c, #e3bb2c)",
          Purple: "linear-gradient(to bottom, #7e4da6, #9b4cc6, #bd44e4)",
          Blue: "linear-gradient(to bottom, #5d8fa8, #72aae3, #8bb6ff)",
          Green: "linear-gradient(to bottom, #5ca862, #61c370, #63dd7e)",
          Gray: "linear-gradient(to bottom, #6c6969, #8c8989, #9c9a9a)"
        }[props.bgImg]
      : "none"};
  background-size: ${(props) => props.bgSize};
  background-position: ${(props) => props.bgPosition || "center"};
`;

export const AbsoluteBox = styled.div`
  position: absolute;
  top: ${(props) => props.top};
  bottom: ${(props) => props.bottom};
  left: ${(props) => props.left};
  right: ${(props) => props.right};
  z-index: ${(props) => props.zIndex};
`;

export const CardCarpet = styled.div`
  padding: 1rem 1rem;
  border-radius: 0.5rem;
  background-color: ${colorCodes.cardBg1};
  .card_content {
    height: 25rem;
  }
`;

export const ModalCarpet = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const ModalInner = styled.div`
  width: 90vw;
  height: 90vh;
  border-radius: 0.625rem;
  background-color: ${colorCodes.cardBg2};
  box-shadow: 0 0 3px white;
  @media (min-width: 440px) {
    width: 398.45px;
  }
  @media (min-width: 610px) {
    width: 586px;
  }
  @media (min-width: 769px) {
    width: 722px;
  }
  @media (min-width: 1025px) {
    height: 85vh;
    width: 944px;
  }
`;

export const ModalGridItem = styled.div`
  flex-grow: 0;
  max-width: 33%;
  flex-basis: 33%;
  position: relative;
  &.w-vision {
    padding: 0.375rem;
  }
  &.wo-vision {
    padding: 0.25rem;
  }
  @media (min-width: 360px) {
    &.w-vision {
      padding: 0.75rem 0.75rem 0.375rem 0.375rem;
    }
    &.wo-vision {
      padding: 0.5rem;
    }
  }
  @media (min-width: 610px) {
    max-width: 20%;
    flex-basis: 20%;
    &.w-vision {
      padding: 0.5rem;
    }
  }
  @media (min-width: 769px) {
    max-width: 16.66667%;
    flex-basis: 16.66667%;
  }
  @media (min-width: 1025px) {
    max-width: 12.5%;
    flex-basis: 12.5%;
  }
`;

// used in MyArtifacts, MyWeapons & MySetups => index
export const MyStyledInv = styled.div`
  position: relative;
  width: calc(100% - 3rem);
  .btn_bar {
    position: absolute;
    bottom: calc(100% + 1rem);
    left: 0;
    padding-left: 0.5rem;
    display: flex;
    align-items: center;
  }
  .body {
    padding-bottom: 1.5rem;
    display: flex;
    overflow: auto;
  }
  @media (min-width: 1050px) {
    width: 1020px;
  }
`;

// used in MyArtifacts & MyWeapons => LeftSection
export const MyStyledRack = styled.div`
  width: 43rem;
  min-width: 20.75rem;
  height: 33rem;
  padding-right: 1rem;
  .cell {
    flex-grow: 0;
    max-width: 33.33%;
    flex-basis: 33.33%;
    padding: 0.5rem;
  }
  @media (min-width: 768px) {
    .cell {
      max-width: 25%;
      flex-basis: 25%;
    }
  }
  @media (min-width: 896px) {
    .cell {
      max-width: 20%;
      flex-basis: 20%;
    }
  }
  @media (min-width: 992px) {
    .cell {
      max-width: 16.66%;
      flex-basis: 16.66%;
    }
  }
`;

// used in Readers => ArtInventory, WpInventory
export const InventoryCell = styled.div`
  position: relative;
  flex-grow: 0;
  max-width: 33.33%;
  flex-basis: 33.33%;
  padding: 0.5rem;
  @media (min-width: 769px) {
    max-width: 25%;
    flex-basis: 25%;
  }
  @media (min-width: 1025px) {
    max-width: 16.66%;
    flex-basis: 16.66%;
  }
`;
