import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        After unleashing Tidecaller with its maximum DMG Bonus, Beidou's{" "}
        <LGreen>Normal and Charged Attacks DMG</LGreen> and{" "}
        <LGreen>ATK SPD</LGreen> is increased by <Green>15%</Green> for 10s.
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs, bonuses }) => {
      bonuses.NA.DMG += 15;
      bonuses.CA.DMG += 15;
      ATTRs["Normal ATK SPD"] += 15;
      ATTRs["Charged ATK SPD"] += 15;
    }
  },
  {
    src: "Constellation 4",
    desc: () => (
      <>
        Within 10s of taking DMG, Beidou's <LGreen>Normal Attacks</LGreen> gain{" "}
        <Green>20%</Green> additional <LGreen>Electro DMG</LGreen>.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    affect: "self"
  }
];

export const debuffs = [
  {
    src: "Constellation 6",
    desc: () => (
      <>
        During the duration of Stormbreaker, the <LGreen>Electro RES</LGreen> of
        surrounding opponents is decreased by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    addPntes: ({ rdMult }) => {
      rdMult["Electro RES"] -= 15;
    }
  }
];
