import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Constellation 1",
    desc: () => (
      <>
        Increases <LGreen>Gale Blade's DMG</LGreen> by <Green>40%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.ES.DMG += 40;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        When Jean picks up an Elemental Orb/Particle, all party members have
        their <LGreen>ATK SPD</LGreen> increased by <Green>15%</Green> for 15s.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 15;
      ATTRs["Charged ATK SPD"] += 15;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 4",
    desc: () => (
      <>
        Within the Field created by Dandelion Breeze, all opponents have their{" "}
        <LGreen>Anemo RES</LGreen> decreased by <Green>40%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 4,
    addPntes: ({ rdMult }) => {
      rdMult["Anemo RES"] -= 40;
    }
  }
];
