import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Increases <LGreen>Energy Recharge</LGreen> by <Green>16%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Energy Recharge"] += 16;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 6",
    desc: () => (
      <>
        Gust Surge decreases opponents' <LGreen>Anemo RES</LGreen> and{" "}
        <LGreen>RES</LGreen> towards the <LGreen>Element absorbed</LGreen> by{" "}
        <Green>20%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    selfLabels: ["Element Absorbed"],
    labels: ["Element Absorbed"],
    inputTypes: ["absorption"],
    addPntes: ({ rdMult, inputs }) => {
      rdMult["Anemo RES"] -= 20;
      rdMult[inputs[0] + " RES"] -= 20;
    }
  }
];
