import { useState, useContext } from "react";
import { SetDBCt } from "../../../App";
import { Box, CardCarpet } from "../../../styledCpns/Layout";
import { ButtonBar } from "../../CommonCpns";
import MyWpCard from "../../Readers/MyWpCard";
import WpInventory from "../../Readers/WpInventory";
import { deepCopy } from "../../../helpers/misc";

export default function SectionWp(props) {
  const { weapon, owner } = props;
  const [wpInvOn, setWpInvOn] = useState(false);
  const { setMyChars, setMyWps } = useContext(SetDBCt);
  return (
    <CardCarpet className="ml-3">
      <Box className="hide-sb" width="18.75rem" height="25.5rem">
        <MyWpCard weapon={weapon} mutable={true} />
      </Box>
      <ButtonBar
        className="mt-3"
        texts={["Switch"]}
        variants={["positive"]}
        handlers={[() => setWpInvOn(true)]}
      />
      {wpInvOn && (
        <WpInventory
          owner={owner}
          wpType={weapon.type}
          weaponID={weapon.ID}
          close={() => setWpInvOn(false)}
          btnText="Switch"
          btnClick={(targetWp) => {
            setMyWps((prev) => {
              const newWps = deepCopy(prev);
              newWps[
                newWps.findIndex((wp) => wp.ID === targetWp.ID)
              ].user = owner;
              newWps[props.weaponI].user = targetWp.user;
              return newWps;
            });
            setMyChars((prev) => {
              const newChars = deepCopy(prev);
              newChars[props.ownerIndex].weaponID = targetWp.ID;
              if (targetWp.user) {
                newChars[
                  newChars.findIndex((char) => char.name === targetWp.user)
                ].weaponID = weapon.ID;
              }
              return newChars;
            });
          }}
        />
      )}
    </CardCarpet>
  );
}
