import { LGreen, Green } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Constellation 2",
    desc: () => (
      <>
        Qiqi's <LGreen>Normal and Charged Attack DMG</LGreen> against opponents
        affected by Cryo is increased by <Green>15%</Green>.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 15;
      bonuses.CA.DMG += 15;
    }
  }
];
