import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => (
      <>
        When Sucrose triggers a Swirl, all characters in the party with the
        matching element have their <LGreen>Elemental Mastery</LGreen> increased
        by <Green>50</Green> for 8s.
      </>
    ),
    affect: "teammates",
    addBnes: ({ ATTRs }) => {
      ATTRs["Elemental Mastery"] += 50;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ inputs }) => (
      <>
        When Astable Anemohypostasis Creation - 6308 or Forbidden Creation -
        Isomer 75 / Type II hits an opponent, increases all party members'{" "}
        <LGreen>Elemental Mastery</LGreen> based on <Green>20%</Green> of
        Sucrose's <LGreen>Elemental Mastery</LGreen> for 8s.{" "}
        <Red>Elemental Mastery Bonus: {buffs[1].bnValue(inputs)}.</Red>
      </>
    ),
    affect: "teammates",
    labels: ["Elemental Mastery"],
    inputs: [0],
    inputTypes: ["text"],
    maxs: [9999],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Elemental Mastery"] += buffs[1].bnValue(inputs);
    },
    bnValue: ([EM]) => Math.round(EM * 0.2)
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        If Forbidden Creation - Isomer 75 / Type II triggers an Elemental
        Absorption, all party members gain a <Green>20%</Green>{" "}
        <LGreen>Elemental DMG Bonus</LGreen> for the corresponding absorbed
        element during its duration.
      </>
    ),
    affect: "teammates",
    labels: ["Element Absorbed"],
    inputs: ["Pyro"],
    inputTypes: ["swirl"],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs[inputs[0] + " DMG Bonus"] += 20;
    }
  }
];
