import styled from "styled-components";
import { ModalGridItem, Box } from "../../styledCpns/Layout";
import { Img, Text } from "../../styledCpns/DataDisplay";
import { colorCodes, wikia, visionIcons } from "../../data/misc";

let Badge = styled.div`
  position: absolute;
  top: 0.125rem;
  right: 0.125rem;
  padding: 4px;
  border-radius: 50%;
  background-color: black;
  box-shadow: 0 0 2px white;
  &.w-cons {
    display: flex;
    border-radius: 1rem;
    padding-left: 6px;
  }
  .cons {
    margin-right: 2px;
    color: ${colorCodes.green};
  }
`;

export default function Body({ data, forChar, pick }) {
  return (
    <div className="pr-3 full-h overflow-auto custom-sb">
      <div className="flex-wrap">
        {data.map((item, i) => {
          const hasCons = item.constellation !== undefined;
          return (
            <ModalGridItem
              key={i}
              className={forChar ? "w-vision" : "wo-vision"}
            >
              <Thumb pick={() => pick(item)} item={item} forChar={forChar} />
              {forChar && (
                <Badge className={hasCons ? "w-cons" : ""}>
                  {hasCons && <p className="cons">C{item.constellation}</p>}
                  <Img
                    width="20px"
                    src={wikia + visionIcons[item.vision] + ".png"}
                    alt="vision"
                  />
                </Badge>
              )}
            </ModalGridItem>
          );
        })}
      </div>
    </div>
  );
}

function Thumb(props) {
  const { item } = props;
  return (
    <div className="pointer zoomin-on-hover" onClick={props.pick}>
      <Box
        className={"overflow-hidden flex " + (props.forChar ? "pt-3" : "p-1")}
        borderRadius="0.5rem 0.5rem 0 0"
        bgImg={item.rarity}
      >
        <Img src={wikia + item.icon + ".png"} alt="" width="100%" />
      </Box>
      <Box borderRadius="0 0 0.5rem 0.5rem" bgColor="defaultBg">
        <Text
          className="mx-2 ellipsis"
          color="black"
          fontWeight={700}
          align="center"
        >
          {item.name}
        </Text>
      </Box>
    </div>
  );
}
