import { useState, useContext } from "react";
import { MyWpsCt } from "../../App";
import {
  Box,
  ModalCarpet,
  ModalInner,
  CardCarpet,
  InventoryCell
} from "../../styledCpns/Layout";
import { Text } from "../../styledCpns/DataDisplay";
import { ButtonBar, EquippedChar } from "../CommonCpns";
import Heading from "../Picker/Heading";
import Thumb from "./Thumb";
import MyWpCard from "./MyWpCard";
import weapons from "../../data/weapons";
import { useCloseModal } from "../../helpers/hooks";

export default function WpInventory(props) {
  const { wpType } = props;
  const myWeapons = useContext(MyWpsCt);
  const filteredData = myWeapons.filter((wp) => wp.type === wpType);
  const weaponI = filteredData.findIndex(
    (weapon) => weapon.ID === props.weaponID
  );
  const [chosenI, setChosenI] = useState(weaponI !== -1 ? weaponI : 0);
  const chosenWp = filteredData[chosenI];
  const ref = useCloseModal(props.close);

  return (
    <ModalCarpet>
      <ModalInner ref={ref}>
        <Box height="10%" className="p-2">
          <Heading dataType={wpType} close={props.close} />
        </Box>
        <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
          <div className="full-h flex hide-sb">
            {filteredData.length ? (
              <Rack
                data={filteredData}
                chosenI={chosenI}
                setChosenI={setChosenI}
              />
            ) : (
              <div
                className="pt-5 px-3 grow-1 flex justify-center"
                style={{ minWidth: "18.75rem" }}
              >
                <Text variant="h5">
                  Add at least a {wpType} to "My Weapons" to use this feature.
                </Text>
              </div>
            )}
            <div className="flex-col justify-between">
              <CardCarpet>
                <Box className="hide-sb" width="17rem" height="25rem">
                  {chosenWp ? (
                    <MyWpCard weapon={chosenWp} mutable={false} />
                  ) : null}
                </Box>
                {chosenWp && chosenWp.user !== props.owner ? (
                  <ButtonBar
                    className="mt-3"
                    variants={["positive"]}
                    texts={[props.btnText]}
                    handlers={[
                      () => {
                        props.btnClick(chosenWp);
                        props.close();
                      }
                    ]}
                  />
                ) : null}
              </CardCarpet>
              {chosenWp?.user ? <EquippedChar user={chosenWp.user} /> : null}
            </div>
          </div>
        </Box>
      </ModalInner>
    </ModalCarpet>
  );
}

function Rack(props) {
  const [clickedI, setClickedI] = useState(null);
  return (
    <div
      className="pr-2 grow-1 full-h overflow-auto hide-sb"
      style={{ minWidth: "18.75rem" }}
    >
      <div className="flex-wrap">
        {props.data.map((wp, i) => {
          const { icon, rarity } = weapons[wp.type][wp.name];
          return (
            <InventoryCell key={i}>
              <Thumb
                item={wp}
                icon={icon}
                rarity={rarity}
                chosen={i === props.chosenI}
                clicked={i === clickedI}
                handleMouseDown={() => setClickedI(i)}
                choose={() => {
                  setClickedI(null);
                  props.setChosenI(i);
                }}
              />
            </InventoryCell>
          );
        })}
      </div>
    </div>
  );
}
