import { useState, useContext } from "react";
import { MyArtsCt } from "../../App";
import {
  Box,
  ModalCarpet,
  ModalInner,
  CardCarpet,
  InventoryCell
} from "../../styledCpns/Layout";
import { Text } from "../../styledCpns/DataDisplay";
import { ButtonBar, EquippedChar } from "../CommonCpns";
import Heading from "../Picker/Heading";
import Thumb from "./Thumb";
import MyArtCard from "./MyArtCard";
import artifacts from "../../data/artifacts";
import { artNames } from "../../data/misc";
import { useCloseModal } from "../../helpers/hooks";

export default function ArtInventory(props) {
  const { artType } = props;
  const myArtifacts = useContext(MyArtsCt);
  const filteredData = myArtifacts.filter((wp) => wp.type === artType);
  const artI =
    artType === props.artIDs
      ? filteredData.findIndex((art) => art.ID === props.artIDs[artType])
      : -1;
  const [chosenI, setChosenI] = useState(artI !== -1 ? artI : 0);
  const chosenArt = filteredData[chosenI];
  const ref = useCloseModal(props.close);

  return (
    <ModalCarpet>
      <ModalInner ref={ref}>
        <Box height="10%" className="p-2">
          <Heading dataType={artType} close={props.close} />
        </Box>
        <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
          <div className="full-h flex hide-sb">
            {filteredData.length ? (
              <Rack
                data={filteredData}
                chosenI={chosenI}
                setChosenI={setChosenI}
              />
            ) : (
              <div
                className="pt-5 px-3 grow-1 flex justify-center"
                style={{ minWidth: "18.75rem" }}
              >
                <Text variant="h5">
                  Add at least a {artNames[artType]} to "My Artifacts" to use
                  this feature.
                </Text>
              </div>
            )}
            <div className="flex-col justify-between">
              <CardCarpet>
                <Box className="hide-sb" width="16rem" height="24rem">
                  {chosenArt ? (
                    <MyArtCard art={chosenArt} mutable={false} ssSpace={3} />
                  ) : null}
                </Box>
                {chosenArt && chosenArt.user !== props.owner ? (
                  <ButtonBar
                    className="mt-4"
                    variants={["positive"]}
                    texts={[props.btnText]}
                    handlers={[
                      () => {
                        props.btnClick(chosenArt);
                        props.close();
                      }
                    ]}
                  />
                ) : null}
              </CardCarpet>
              {chosenArt?.user ? <EquippedChar user={chosenArt.user} /> : null}
            </div>
          </div>
        </Box>
      </ModalInner>
    </ModalCarpet>
  );
}

function Rack(props) {
  const [clickedI, setClickedI] = useState(null);
  return (
    <div
      className="pr-2 grow-1 full-h overflow-auto hide-sb"
      style={{ minWidth: "18.75rem" }}
    >
      <div className="flex-wrap">
        {props.data.map((art, i) => {
          const { icon, rarity = "Gold" } = artifacts[art.set][art.type];
          return (
            <InventoryCell key={i}>
              <Thumb
                item={art}
                icon={icon}
                rarity={rarity}
                chosen={i === props.chosenI}
                clicked={i === clickedI}
                handleMouseDown={() => setClickedI(i)}
                choose={() => {
                  setClickedI(null);
                  props.setChosenI(i);
                }}
              />
            </InventoryCell>
          );
        })}
      </div>
    </div>
  );
}
