import styled from "styled-components";
import { useState } from "react";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn } from "../../styledCpns/Inputs";
import { Box, AbsoluteBox } from "../../styledCpns/Layout";
import { CloseBtn } from "../CommonCpns";
import Filter from "./Filter";
import { artNames } from "../../data/misc";

// also used in Readers => WpInventory & ArtInventory
export default function Heading(props) {
  const [filterOn, setFilterOn] = useState(false);
  const { dataType, massAdd, needMassAdd } = props;
  return (
    <Box
      className="pl-3 full-h flex align-center relative"
      borderRadius="0.5rem 0.5rem 0 0"
      bgColor="orange"
    >
      {(window.innerWidth >= 769 || !needMassAdd) && (
        <Text className="full-w" variant="h4" align="center" color="black">
          {artNames[dataType] || dataType}
        </Text>
      )}
      {dataType === "Character" && (
        <>
          <FilterBtn
            className="glow-on-hover"
            handleClick={() => setFilterOn(!filterOn)}
          />
          <Filter
            filterOn={filterOn}
            setFilterOn={setFilterOn}
            filter={props.filter}
            setFilter={props.setFilter}
          />
        </>
      )}
      {needMassAdd && (
        <MassAddArea className="flex align-center">
          <input
            type="checkbox"
            value={massAdd}
            onChange={() => props.setMassAdd(!massAdd)}
          />
          <Text className="mt-1 ml-2" fontWeight={700} color="black">
            Mass Add
          </Text>
        </MassAddArea>
      )}
      <AbsoluteBox top="0.5rem" right="0.5rem">
        <CloseBtn bgColor="black" onClick={props.close} />
      </AbsoluteBox>
    </Box>
  );
}

const FilterBtn = styled(({ handleClick, ...others }) => (
  <AbsoluteBox top="50%" left="20px">
    <IconBtn
      icon="fas fa-filter"
      size="1.75rem"
      fontSize="0.825rem"
      bgColor="black"
      color="lightGold"
      {...others}
      onClick={handleClick}
    />
  </AbsoluteBox>
))`
  margin-top: -0.825rem;
`;

const MassAddArea = styled.div`
  position: absolute;
  right: 4rem;
  input {
    transform: scale(1.6);
  }
`;
