import { useState } from "react";
import { MyStyledRack } from "../../styledCpns/Layout";
import Thumb from "../Readers/Thumb";
import artifacts from "../../data/artifacts";

export default function LeftSection(props) {
  const [clickedI, setClickedI] = useState(null);
  return (
    <MyStyledRack className="hide-sb">
      <div className="flex-wrap">
        {props.arts.map((art, i) => {
          const { icon } = artifacts[art.set][art.type];
          return (
            <div key={i} className="cell relative">
              <Thumb
                item={art}
                icon={icon}
                rarity="Gold"
                chosen={i === props.chosenI}
                clicked={i === clickedI}
                handleMouseDown={() => setClickedI(i)}
                choose={() => {
                  setClickedI(null);
                  props.setChosenI(i);
                }}
              />
            </div>
          );
        })}
      </div>
    </MyStyledRack>
  );
}
